<template>
  <div :id="containerID.replace('#', '')"/>
</template>


<script>
import * as d3 from 'd3'

const componentName = 'QueueChart'
const timeScale = 1.0
const precision = 0.1
const second = 1.0
const millisecondsInSecond = 1000.0 / timeScale
const stationsColors = ['#f7cc05', '#eb2d23', '#1c3f91', '#079bd7', '#00883a', '#f0859d', '#ae5e3b']

export default {
  name: componentName,
  data() {
    return {
      rootGroup: Selection,
      stationsGroup: Selection,
      passengersGroup: Selection,
      statisticsGroup: Selection,
      stations: [],
      passengers: [],
      generalPassengersQueue: [],
      arrivalPassengerIndex: 0,
      lastArrivalTime: 0.0,
      getPositionInGeneralQueue: undefined,
      statisticsTimerId: 0,
      time: 0.0,
      serviceTimes: [],
      waitingTimes: [],
      statistics: [{label: 'Work time', value: 0.0}, {label: 'Waiting probability', value: 0.0},
        {label: 'Average waiting time', value: 0.0}, {label: 'Average service time', value: 0.0},
        {label: 'Server idle probability', value: 0.0}, {label: 'Average time between arrivals', value: 0.0},
        {label: 'Average waiting time who waited', value: 0.0}, {label: 'Passengers count', value: 0.0}]
    }
  },
  props: {
    id: {type: String, required: true},
    margin: {type: Object, required: true},
    width: {type: Number, required: true},
    height: {type: Number, required: true},
    staticStationsCount: {type: Number, required: true},
    passengersCount: {type: Number, required: true},
    arrivalInterval: {type: Number, required: true},
    takeInterval: {type: Number, required: true},
    isStationHasQueue: {type: Boolean, required: true},
  },
  computed: {
    containerID() {
      return `#${componentName}Container${this.id}`
    },
    visibleAreaID() {
      return `#${componentName}VisibleDefs${this.id}`
    },
    innerWidth() {
      return this.width - this.margin.left - this.margin.right
    },
    innerHeight() {
      return this.height - this.margin.top - this.margin.bottom
    },
    passengerGraphicModelSize() {
      return this.innerHeight / ((this.staticStationsCount + 1.0) * 2.0)
    },
  },
  mounted() {
    this.rootGroup = addRootGroup(this.containerID, this.width, this.height, this.margin)
    this.stationsGroup = this.rootGroup.append('g')
    this.passengersGroup = this.rootGroup.append('g')
    this.statisticsGroup = this.rootGroup.append('g')
    this.generatePassengers(this.passengersCount, this.passengers, this.arrivalInterval)
    this.generateStations(this.stationsGroup, this.stations, this.staticStationsCount)
    this.getPositionInGeneralQueue = this.isStationHasQueue ? this.getPositionWithoutGeneralQueue : this.getPositionWithGeneralQueue
    this.getFreeStation = this.isStationHasQueue ? this.getFreeStationWithoutGeneralQueue : this.getFreeStationWithGeneralQueue
    this.statistics[5].value = this.passengers.map((p, i) => p.arrivalTime - (i > 0 ? this.passengers[i - 1].arrivalTime : 0)).reduce((a, b) => a + b, 0.0)
    this.statistics[5].value /= this.passengers.length
    this.time = precision
    setInterval(() => this.time += precision * second, millisecondsInSecond * precision)
    this.addStatistics()
    this.statisticsTimerId = setInterval(this.addStatistics, millisecondsInSecond)
    this.arrivePassenger()
  },
  methods: {
    addStatistics() {
      const workingTime = this.time
      this.statistics[0].value = workingTime
      this.statistics[7].value = this.passengersCount - this.arrivalPassengerIndex
      const serviceTime = this.serviceTimes.reduce((a, b) => a + b, 0.0)
      this.statistics[3].value = serviceTime / (this.serviceTimes.length || 1)
      const filtered = this.waitingTimes.filter((t) => t > second - precision)
      const waitingTime = filtered.reduce((a, b) => a + b, 0.0)
      this.statistics[6].value = waitingTime / (filtered.length || 1)
      this.statistics[2].value = waitingTime / (this.waitingTimes.length || 1)
      this.statistics[1].value = filtered.length / (this.waitingTimes.length || 1)
      this.statistics[4].value = 1.0 - Math.min(serviceTime / ((workingTime * this.staticStationsCount) || 1), 1.0)

      const statisticsSelection = this.statisticsGroup.selectAll('text').data(this.statistics)
      statisticsSelection.enter()
          .append('text')
          .attr('x', 10)
          .attr('y', (_, i) => (i + 1) * 20.0)
          .merge(statisticsSelection)
          .text((d) => `${d.label}: ${d.value.toFixed(2)}`)
      statisticsSelection.exit().remove()
    },

    generatePassengers(passengersCount, passengers, arrivalInterval) {
      let arrivalTime = 0
      for (let i = 0; i < passengersCount; i++) {
        arrivalTime += generateInterval(arrivalInterval)
        const graphicObject = this.passengersGroup.append('rect')
            .attr('x', -this.passengerGraphicModelSize)
            .attr('y', (this.innerHeight - this.passengerGraphicModelSize) * 0.5)
            .attr('width', this.passengerGraphicModelSize)
            .attr('height', this.passengerGraphicModelSize)
        passengers.push({number: i, arrivalTime, graphicObject})
      }

      return passengers
    },

    generateStations(group, stations, stationsCount) {
      const heightBias = this.innerHeight / (stationsCount + 1)
      const lineWidth = this.passengerGraphicModelSize * 2.0

      for (let i = 0; i < stationsCount; i++) {
        group.append('rect')
            .attr('x', this.innerWidth - lineWidth)
            .attr('y', (heightBias * (1 + i)) - (this.passengerGraphicModelSize * 0.25))
            .attr('width', lineWidth + this.passengerGraphicModelSize)
            .attr('height', this.passengerGraphicModelSize * 0.5)
            .attr('fill', stationsColors[i % stationsColors.length])
            .classed('stationLine', true)

        const platform = group.append('rect')
            .attr('x', this.innerWidth - lineWidth)
            .attr('y', (heightBias * (1 + i)) - (this.passengerGraphicModelSize * 0.5))
            .attr('width', this.passengerGraphicModelSize * 0.3)
            .attr('height', this.passengerGraphicModelSize)
            .attr('fill', stationsColors[i % stationsColors.length])
            .classed('stationLine', true)

        const train = group.append('rect')
            .attr('x', this.innerWidth)
            .attr('y', (heightBias * (1 + i)) - (this.passengerGraphicModelSize * 0.5))
            .attr('width', this.passengerGraphicModelSize)
            .attr('height', this.passengerGraphicModelSize)
            .classed('train', true)

        stations.push({number: i, platform, train, passengersQueue: []})
      }
    },

    arrivePassenger() {
      if (this.arrivalPassengerIndex >= this.passengers.length) {
        return
      }

      const passenger = this.passengers[this.arrivalPassengerIndex]
      const timeout = (passenger.arrivalTime - this.lastArrivalTime - second) * millisecondsInSecond
      this.lastArrivalTime = passenger.arrivalTime
      setTimeout(() => passenger.graphicObject.transition()
              .duration(millisecondsInSecond)
              .attr('x', this.getPositionInGeneralQueue(this.generalPassengersQueue.length))
              .on('end', () => {
                this.time = passenger.arrivalTime
                this.generalPassengersQueue.push(this.passengers[this.arrivalPassengerIndex])
                this.arrivalPassengerIndex++
                passenger.graphicObject.transition()
                    .duration(millisecondsInSecond * 0.5)
                    .attr('x', this.getPositionInGeneralQueue(this.generalPassengersQueue.length - 1))
                this.onArrivedPassenger()
              }),
          timeout
      )
    },

    getPositionWithGeneralQueue(number) {
      return (this.innerWidth * 0.5) + ((1 - number) * 1.5 * this.passengerGraphicModelSize)
    },

    getPositionWithoutGeneralQueue() {
      return 1.5 * this.passengerGraphicModelSize
    },

    getPositionInStationQueue(station, number) {
      return station.platform.attr('x') - this.passengerGraphicModelSize - (1.5 * this.passengerGraphicModelSize * number)
    },

    onArrivedPassenger() {
      this.trySelectStation()
      this.arrivePassenger()
    },

    trySelectStation() {
      if (this.arrivalPassengerIndex >= this.passengers.length) {
        const busyStations = this.stations.filter(x => x.passengersQueue.length > 0)
        if (busyStations.length === 0) {
          clearInterval(this.statisticsTimerId)
        }
      }

      if (this.generalPassengersQueue.length === 0) {
        return
      }

      const freeStation = this.getFreeStation()
      if (freeStation === undefined) {
        return
      }

      const passenger = this.generalPassengersQueue.shift()
      this.orderGeneralQueue()

      freeStation.passengersQueue.push(passenger)
      passenger.graphicObject.attr('fill', freeStation.platform.attr('fill'))
          .transition()
          .duration(millisecondsInSecond * 0.5)
          .attr('x', this.getPositionInStationQueue(freeStation, freeStation.passengersQueue.length - 1))
          .attr('y', freeStation.platform.attr('y'))
          .on('end', () => {
                passenger.graphicObject.transition()
                    .duration(millisecondsInSecond * 0.25)
                    .attr('x', this.getPositionInStationQueue(freeStation, freeStation.passengersQueue.indexOf(passenger)))
              }
          )

      if (freeStation.passengersQueue.length === 1) {
        this.takePassenger(passenger, freeStation)
      }

      this.trySelectStation()
    },

    getFreeStationWithGeneralQueue() {
      let freeStation = undefined
      const freeStations = this.stations.filter(x => x.passengersQueue.length === 0)
      if (freeStations.length > 0) {
        freeStation = freeStations[Math.floor(Math.random() * freeStations.length)]
      }

      return freeStation
    },

    getFreeStationWithoutGeneralQueue() {
      const minLength = this.stations.reduce((min, x) => Math.min(min, x.passengersQueue.length), this.stations[0].passengersQueue.length)
      const freeStations = this.stations.filter(x => x.passengersQueue.length === minLength)
      return freeStations[Math.floor(Math.random() * freeStations.length)]
    },

    orderGeneralQueue() {
      this.generalPassengersQueue.forEach((x, i) => x.graphicObject.transition()
          .duration(millisecondsInSecond * 0.5)
          .attr('x', this.getPositionInGeneralQueue(i))
      )
    },

    orderStationQueue(station) {
      station.passengersQueue.forEach((x, i) => x.graphicObject.transition()
          .duration(millisecondsInSecond * 0.5)
          .attr('x', this.getPositionInStationQueue(station, i))
          .attr('y', station.platform.attr('y'))
      )
    },

    takePassenger(passenger, station) {
      this.waitingTimes.push(this.time - passenger.arrivalTime)
      const serviceTime = generateInterval(this.takeInterval)
      this.serviceTimes.push(serviceTime)
      const duration = serviceTime * millisecondsInSecond * 0.5
      station.train.transition()
          .duration(duration)
          .attr('x', station.platform.attr('x') - this.passengerGraphicModelSize)
          .on('end', () => {
            passenger.graphicObject.transition()
                .duration(duration)
                .attr('x', this.innerWidth)

            station.train.transition()
                .duration(duration)
                .attr('x', this.innerWidth)
                .on('end', () => {
                  station.passengersQueue.shift()
                  if (station.passengersQueue.length > 0) {
                    this.orderStationQueue(station)
                    this.takePassenger(station.passengersQueue[0], station)
                  } else {
                    this.trySelectStation()
                  }
                })
          })
    },
  }
}

function addRootGroup(containerID, width, height, margin) {
  return d3.select(containerID)
      .append('svg')
      .attr('width', width)
      .attr('height', height)
      .append('g')
      .attr('transform', `translate(${margin.left}, ${margin.top})`)
}

function generateInterval(interval) {
  return second + Math.floor(Math.random() * interval)
}
</script>

<style>
.train {
  stroke-width: 4px;
  stroke: black;
  fill: none;
}
</style>